.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto; }

html, body {
  height: 100%; }

body {
  display: flex;
  flex-direction: column;
  justify-content: space-between; }

main {
  margin-top: 62px; }
  @media (min-width: 768px) {
    main {
      margin-top: 74px; } }

ul, li, p {
  margin: 0;
  padding: 0;
  list-style: none; }

h1, h2, h3, h4, h5, h6 {
  line-height: 1.5;
  font-weight: bold;
  margin: 0; }

.modal-body {
  max-height: 90vh;
  overflow-y: auto; }

.rounded-xl {
